@import "~antd/dist/antd.css";

html,
body {
  height: 100%;
}

body {
  margin: 0;
  font-family: Muli, serif;
  font-weight: 200;
}

#root {
  height: 100%;
}

.pageContainer {
  text-align: center;
  display: flex;
  min-height: 100%;
}

.section {
  flex: 1;
}

.section--description {
  background-color: #0d8050;
  background-image: radial-gradient(circle at bottom, #0f975e, #064028);
  color: white;
  font-size: 1.5em;
  padding: 0 50px;
}

.description-link {
  font-size: 1em;
  font-weight: 200;
  color: white;
}

.section--description .title {
  font-size: 2em;
  font-weight: 200;
}

.monthly-payment.desktop .description {
  font-size: 1em;
  padding: 0 50px;
  margin-bottom: 150px;
}

.monthly-payment.mobile .description {
  padding: 0 10px;
}

.monthly-payment.desktop .input-error {
  padding: 0;
  color: white;
  font-size: 40px;
}

.section--results {
  color: black;
  font-size: 1.2em;
  padding: 25px 0;
}

.section--input {
  flex-flow: column;
}

.sub-section-input {
  height: 90%;
}

.sub-section-navigation {
  height: 10%;
  padding: 30px 10px;
}

.flex-vertical-center {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.padding-left {
  padding-left: 10px;
}

.text-align-right {
  text-align: right;
}

.padding-right {
  padding-right: 8px;
}

.text-align-left {
  text-align: left;
}

.padding-left {
  padding-left: px;
}

.button-container {
  width: 50%;
  display: inline-block;
}
.button-left {
  text-align: left;
}
.button-right {
  text-align: right;
}

.spinner {
  position: absolute;
  top: 10px;
  right: 10px;
  z-index: 2;
}

.input-error {
  padding-left: 5px;
  font-size: 24px;
}

.anticon-loading {
  font-size: 32px;
}

.dropdown-make {
  font-size: 1.5em;
  cursor: pointer;
}

.anticon-down {
  margin-left: 5px;
}

.ant-input,
.ant-input-number {
  width: 180px;
}

.ant-row-flex .ant-input-number {
  width: 130px;
}

.ant-row {
  width: 100%;
}

.ant-statistic.inline,
.ant-statistic.inline .ant-statistic-content,
.fade-inline {
  display: inline;
}

.desktop {
  display: block;
}

.mobile {
  display: none;
}

.footer {
  text-align: center;
}

.footer-content {
  padding: 30px 0;
}

.ant-divider {
  margin: 0 auto;
  width: 80%;
  min-width: 80%;
}

.footer-icon.desktop {
  font-size: 20px;
  color: white;
  cursor: pointer;
}

.footer-icon.mobile {
  font-size: 20px;
  color: gray;
  cursor: pointer;
}

.footer-icon.desktop:hover {
  color: #5fb2ff;
}

.footer-disclaimer.desktop {
  font-size: 0.8em;
}

.threshold-icon {
  margin-left: 10px;
}

.progress {
  position: absolute;
  line-height: 0;
}

.ant-progress-outer {
  font-size: 5px;
  line-height: 0;
}
.ant-progress-inner {
  background-color: transparent;
}

.tooltip {
  display: block;
  position: absolute;
  width: 300px;
  padding: 5px;
  color: white;
  text-align: center;
  font-size: 0.9em;
  border-radius: 5px;
  background-color: #1890ff;
  box-shadow: 0px 3px 5px lightgray;
  z-index: 1;
}
.tooltip-sellingPrice {
  top: -50px;
  left: -100px;
}

.tooltip-msrpPercent {
  top: -80px;
  left: -100px;
}

.tooltip--hidden {
  display: none;
}

@media only screen and (max-device-width: 768px) {
  .pageContainer {
    flex-direction: column;
    height: 100%;
  }

  .section--description {
    padding: 0 10px;
  }

  .section--description.results {
    background-image: radial-gradient(ellipse at bottom, #0f975e, #064028);
    box-shadow: 0px 3px 10px gray;
    position: fixed;
    width: 100%;
    z-index: 1;
  }

  .section--results {
    font-size: 1.2em;
    margin-top: 30px;
  }

  .spinner {
    position: fixed;
  }

  .desktop {
    display: none;
  }

  .mobile {
    display: block;
  }

  .monthly-payment {
    font-size: 0.5em;
    padding: 10px 0;
    height: 50px;
  }

  .sub-section-input {
    height: 80%;
  }

  .sub-section-navigation {
    height: 20%;
    padding: 0;
    padding: 15px 10px 0;
  }

  .ant-row-flex .ant-input-number {
    width: 100px;
  }

  .interactive-desc {
    padding: 0 10px;
    margin: 15px 0;
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
    transform: translateY(-15px);
  }
  100% {
    opacity: 1;
  }
}

@keyframes fadeOut {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
    transform: translateY(15px);
  }
}
